/**
 * LineChart  -    
 */

.wello-line-chart {
  width: 100%;
  height: 100%;
  position: relative;

  &__svg {
    width: 100%;
    height: 100%;
    overflow: visible;
  }

  &__empty {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    color: var(--text-secondary);
    font-size: var(--font-size-sm);
  }

  //  
  &__grid-line {
    stroke: var(--border-light);
    stroke-width: 1;
    stroke-dasharray: 2,2;
    opacity: 0.5;
  }

  // 
  &__reference-lines {
    .wello-line-chart__reference-line {
      stroke-width: 1;
      stroke-dasharray: 4,4;
      opacity: 0.7;

      &--optimal {
        stroke: var(--color-success);
      }

      &--min {
        stroke: var(--color-warning);
      }

      &--max {
        stroke: var(--color-danger);
      }
    }
  }

  //  
  &__series {
    .wello-line-chart__area {
      transition: fill-opacity 0.2s ease;
    }

    .wello-line-chart__line {
      transition: stroke-width 0.2s ease;
      
      &:hover {
        stroke-width: 3;
      }
    }

    .wello-line-chart__point {
      transition: all 0.2s ease;
      cursor: pointer;

      &:hover {
        r: 6;
        stroke-width: 3;
      }

      //   
      &--normal {
        fill: var(--color-success) !important;
      }

      &--warning {
        fill: var(--color-warning) !important;
      }

      &--danger {
        fill: var(--color-danger) !important;
      }
    }
  }

  // 
  &__x-axis,
  &__y-axis {
    .wello-line-chart__axis-line {
      stroke: var(--border-medium);
      stroke-width: 1;
    }

    .wello-line-chart__axis-label {
      fill: var(--text-secondary);
      font-size: 12px;
      font-family: var(--font-family-primary);
    }

    .wello-line-chart__axis-title {
      fill: var(--text-primary);
      font-size: var(--font-size-sm);
      font-weight: var(--font-weight-medium);
      font-family: var(--font-family-primary);
    }
  }
}

//    (   )
.wello-chart-tooltip {
  &__header {
    font-size: 10px;
    font-weight: var(--font-weight-medium);
    margin-bottom: 2px;
    color: #6b5b47;
    opacity: 0.9;
  }

  &__value {
    font-size: 11px;
    font-weight: var(--font-weight-semibold);
    color: #5d4e37;
    margin: 0;
  }
}

//  
@media (max-width: 480px) {
  .wello-line-chart {
    &__x-axis,
    &__y-axis {
      .wello-line-chart__axis-label {
        font-size: 12px;
      }
    }

    &__series {
      .wello-line-chart__point {
        r: 3;

        &:hover {
          r: 5;
        }
      }
    }
  }
}

//  
@media (prefers-reduced-motion: reduce) {
  .wello-line-chart {
    &__series {
      .wello-line-chart__area,
      .wello-line-chart__line,
      .wello-line-chart__point {
        transition: none;
      }
    }
  }
}
