@use '../styles/variables' as *;
@use '../styles/mixins' as *;

//  Card   -  |  +  
.card {
  background: $overlay-medium; //     
  backdrop-filter: blur(16px);
  padding: $spacing-md;
  border-radius: $border-radius-xl;
  box-shadow: $shadow-md;
  border: 1px solid $gray-100;
  display: flex;
  flex-direction: column; //   
  justify-content: space-between; //   
  transition: box-shadow 0.3s ease;
  text-align: left;
  min-height: 140px; //   
  
  &:hover {
    box-shadow: $shadow-lg;
  }

  //    
  &--clickable {
    cursor: pointer;
    transition: transform 0.2s ease, box-shadow 0.3s ease;

    &:hover {
      transform: translateY(-2px);
      box-shadow: $shadow-lg;
    }

    &:active {
      transform: translateY(0);
    }

    &:focus {
      outline: 2px solid $brand-brown;
      outline-offset: 2px;
    }
  }
  
  //   -  
  &__icon {
    width: 48px;
    height: 48px;
    border-radius: $border-radius-lg;
    background-color: $brand-brown-light;
    color: $brand-brown;
    @include flex-center;
    flex-shrink: 0;
  }
  
  &__icon-svg {
    width: 28px;
    height: 28px;
    color: $brand-brown;
  }
  
  &__icon-text {
    font-size: 18px;
    font-weight: 700;
    color: $brand-brown;
    line-height: 1;
  }
  
  //    -  ( + )
  &__main-content {
    display: flex;
    align-items: flex-start;
    gap: $spacing-md;
    flex: 1;
  }
  
  //   -  
  &__content {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: $spacing-xs;
  }
  
  //  
  &__title {
    font-size: $font-base; // 16px   
    font-weight: 700;
    color: $gray-800;
    margin: 0;
    line-height: 1.2;
  }
  
  //  
  &__description {
    font-size: $font-sm; // $font-xs 1 
    color: $gray-600;
    line-height: 1.6;
    margin: 0;
    
    strong {
      color: $brand-brown;
      font-weight: 600;
    }
  }
  
  //   -  
  &__shortcut {
    margin-top: $spacing-md;
    padding-top: $spacing-xs;
    padding-right: $spacing-sm; //     
    border-top: 1px solid $gray-300; //    
    min-height: 24px; //  
    display: flex;
    align-items: center;
    justify-content: flex-end; //   
    flex-shrink: 0; //  
  }
  
  &__shortcut-text {
    font-size: $font-sm; // $font-xs 1 
    color: $brand-brown;
    font-weight: 600;
    cursor: pointer;
    text-decoration: none;
    transition: all 0.2s ease;
    
    &:hover {
      color: $brand-brown-hover;
      text-decoration: underline;
    }
    
    //   
    &::before {
      content: " ";
      margin-right: 2px;
      font-weight: 700;
    }
  }
  
  //   (  )
  &--vertical {
    width: 100%;
  }
  
  //   (   ) -   10% 
  &--horizontal {
    // :  73% (66% 10% )
    width: calc(73vw - #{$spacing-md * 2});
    min-width: 231px; // 210px 10% 
    max-width: 317px; // 288px 10% 
    flex-shrink: 0;
    scroll-snap-align: start;
    
    //   
    will-change: transform;
    transform: translateZ(0); // GPU  
    
    //     
    .card__icon {
      width: 36px;
      height: 36px;
      font-size: 16px; //   /  
    }
    
    //    (10% )
    @include respond-to(md) {
      width: 277px; // 252px 10% 
    }
  }
}

//   

//   
.cards-vertical {
  display: flex;
  flex-direction: column;
  gap: $spacing-sm;
  width: 100%;
}

//   
.cards-horizontal {
  display: flex;
  overflow-x: auto;
  overflow-y: hidden;
  gap: $spacing-sm;
  padding: $spacing-sm calc((100vw - 75vw) / 2 + #{$spacing-md}) $spacing-lg calc((100vw - 75vw) / 2 + #{$spacing-md});
  scroll-snap-type: x mandatory;
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  
  //   
  will-change: scroll-position;
  contain: layout style paint;
  
  //    
  @include respond-to(md) {
    padding: $spacing-sm $spacing-md $spacing-lg $spacing-md;
  }
  
  //  
  &::-webkit-scrollbar {
    display: none;
  }
  scrollbar-width: none;
  
  //      ( UI)
}

//   ( )
.swipe-area {
  display: flex;
  flex-direction: column;
  gap: $spacing-sm; //    
  margin-bottom: $spacing-md; //   
  
  &__hint {
    text-align: center;
    font-size: $font-xs;
    color: $gray-500;
    margin-top: 0; //    
    padding: 0;
    line-height: 1.2;
    
    //     ( )
    &--top {
      margin-bottom: $spacing-sm; //  
      margin-top: $spacing-lg; //   
      color: $gray-600; //     
      font-weight: 500; //  
    }
  }
}
