@use '../styles/variables' as *;
@use '../styles/mixins' as *;

// HorizontalLayout  
.horizontal-layout {
  min-height: 100vh;
  padding: 0;
  width: 100%;
  overflow-x: hidden; //   

  &__container {
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    background: $overlay-light; //     
    overflow-y: auto;
    overflow-x: hidden; //   
    position: relative;
    
    // :   ,  
    margin: 0;
    border-radius: 0;
    box-shadow: none;
    
    // :  ,   ,  
    @include respond-to(md) {
      max-width: 448px;
      margin: 0 auto;
      border-radius: $border-radius-xl;
      box-shadow: $shadow-lg;
      max-height: 900px;
      height: 100vh;
    }
  }

  //   
  &__bg-decoration {
    position: absolute;
    width: 256px; // w-64 (16rem = 256px)
    height: 256px; // h-64
    background-color: $brand-brown;
    border-radius: 50%;
    opacity: 0.1;
    filter: blur(48px); // blur-3xl
    z-index: -1;
    
    &--top {
      top: 25%; // top-1/4
      left: -96px; // -left-24 (6rem = 96px)
    }
    
    &--bottom {
      bottom: 25%; // bottom-1/4
      right: -96px; // -right-24
    }
  }

  // 
  &__header {
    flex-shrink: 0;
    z-index: 10;
    position: relative;
    height: 90px; //    
  }
  
  &__header-image {
    position: absolute;
    inset: 0;
    width: 130%; //     
    height: 100%;
    object-fit: cover;
    object-position: right center; //  
    transform: translateX(60px); //  60px 
    z-index: 1;
    opacity: 0.8; //   
  }
  
  &__header-overlay {
    position: absolute;
    inset: 0;
    background: linear-gradient(to right, 
      rgba(247, 232, 211, 0.95) 0%,     //  :  
      rgba(247, 232, 211, 0.7) 30%,    //  : 
      rgba(247, 232, 211, 0.3) 70%,    //  : 
      transparent 100%                  //  :   (  )
    );
    z-index: 2;
  }
  
  &__header-content {
    position: relative;
    z-index: 10;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    padding: $spacing-xs $spacing-lg; //    
  }
  
  &__header-slogan {
    font-size: $font-sm;
    color: $gray-600;
    margin: 0 0 2px 0; //   
  }
  
  &__header-logo {
    margin-top: 2px; //   
  }
  
  &__header-logo-title {
    font-size: $font-lg; //   
    font-weight: 700;
    color: $brand-brown;
    margin: 0;
  }
  
  &__header-logo-subtitle {
    font-size: $font-xs;
    color: $gray-500;
    letter-spacing: 0.1em;
    margin: 0 0 2px 0; //   
  }
  
  &__header-main-title {
    font-size: $font-xl; // 2xl xl (  -1)
    font-weight: 500; //   (900  500)
    color: $gray-800;
    margin: 2px 0 0 0; //   
  }

  //   -  
  &__main {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    padding: $spacing-md $spacing-md $spacing-xs; //    (md  xs)
    z-index: 10;
  }
}

//    
.horizontal-cards {
  &__title {
    font-size: $font-lg;
    font-weight: 700;
    color: $gray-800;
    margin: 0 0 $spacing-md 0;
    text-align: center;
  }
  
  &__subtitle {
    font-size: $font-sm;
    color: $gray-600;
    margin: 0 0 $spacing-sm 0;
    text-align: center;
  }
  
  //   Card.scss .swipe-area 
  
  &__container {
    //   Card.scss .cards-horizontal 
    //       
    display: flex;
    flex-direction: column;
    gap: $spacing-sm;
  }
  
  //   Card.scss .card--horizontal 
}
