/**
 * BaseChart  -    
 *   ,   
 */

.wello-chart {
  background-color: var(--color-gray-0);
  border-radius: var(--border-radius-lg);
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--color-border);
  padding: var(--spacing-md);
  margin-bottom: var(--spacing-md);
  position: relative;
  overflow: hidden;
  transition: var(--transition-ease);

  &:hover {
    box-shadow: var(--shadow-md);
  }

  //  
  &--light {
    background-color: var(--color-gray-0);
    color: var(--color-text-primary);
  }

  &--dark {
    background-color: var(--color-gray-800);
    color: var(--color-text-inverse);
    border-color: var(--color-gray-700);
  }

  //  
  &--loading {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  //  
  &--error {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

//  
.wello-chart__header {
  margin-bottom: var(--spacing-md);
  text-align: center;

  @media (min-width: 768px) {
    text-align: left;
  }
}

.wello-chart__title {
  font-size: var(--font-size-lg);
  font-weight: var(--font-weight-semibold);
  color: var(--color-text-primary);
  margin: 0 0 var(--spacing-xs) 0;
  line-height: var(--line-height-tight);
}

.wello-chart__subtitle {
  font-size: var(--font-size-sm);
  color: var(--color-text-secondary);
  margin: 0;
  line-height: var(--line-height-normal);
}

//   
.wello-chart__content {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

//  
.wello-chart__loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: var(--spacing-md);
  color: var(--color-text-secondary);

  p {
    font-size: var(--font-size-sm);
    margin: 0;
  }
}

.wello-chart__spinner {
  width: 32px;
  height: 32px;
  border: 3px solid var(--color-gray-200);
  border-top: 3px solid var(--color-primary-500);
  border-radius: 50%;
  animation: wello-chart-spin 1s linear infinite;
}

@keyframes wello-chart-spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

//  
.wello-chart__error {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: var(--spacing-sm);
  color: var(--color-text-secondary);
  text-align: center;
  padding: var(--spacing-lg);

  h4 {
    font-size: var(--font-size-base);
    font-weight: var(--font-weight-medium);
    color: var(--color-danger-500);
    margin: 0;
  }

  p {
    font-size: var(--font-size-sm);
    margin: 0;
    max-width: 300px;
  }
}

.wello-chart__error-icon {
  font-size: var(--font-size-2xl);
  opacity: 0.7;
}

// 
.wello-chart__legend {
  margin-top: var(--spacing-md);
  display: flex;
  flex-wrap: wrap;
  gap: var(--spacing-sm);
  justify-content: center;

  @media (min-width: 768px) {
    justify-content: flex-start;
  }
}

.wello-chart__legend-item {
  display: flex;
  align-items: center;
  gap: var(--spacing-xs);
  font-size: var(--font-size-sm);
  color: var(--color-text-secondary);
}

.wello-chart__legend-color {
  width: 12px;
  height: 12px;
  border-radius: var(--border-radius-sm);
  flex-shrink: 0;
}

//  ( ) -   
.wello-chart-tooltip {
  position: absolute;
  background-color: rgba(245, 240, 230, 0.95);
  color: #5d4e37;
  padding: 6px 10px; //    ( )
  border-radius: var(--border-radius-md);
  font-size: var(--font-size-xs);
  line-height: var(--line-height-tight);
  pointer-events: none;
  z-index: 9999; //  z-index   (    )
  box-shadow: 0 4px 12px rgba(139, 125, 107, 0.15);
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
  backdrop-filter: blur(4px);
  border: 1px solid rgba(210, 200, 185, 0.3);
  min-width: 100px;
  max-width: 200px;

  &--visible {
    opacity: 1;
  }

  //   ()
  &::after {
    content: '';
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    border: 5px solid transparent;
    border-top-color: rgba(245, 240, 230, 0.95);
  }
}

//  
@media (max-width: 480px) {
  .wello-chart {
    padding: var(--spacing-sm);
    margin-bottom: var(--spacing-sm);
  }

  .wello-chart__title {
    font-size: var(--font-size-base);
  }

  .wello-chart__subtitle {
    font-size: var(--font-size-xs);
  }
}

//  
@media (prefers-reduced-motion: reduce) {
  .wello-chart__spinner {
    animation: none;
  }
  
  .wello-chart {
    transition: none;
  }
}

//   
@media (prefers-contrast: high) {
  .wello-chart {
    border-width: 2px;
    border-color: var(--color-text-primary);
  }

  .wello-chart__title {
    font-weight: var(--font-weight-bold);
  }
}
