/**
 * ComprehensiveAnalysisPage Styles
 *   :  ,  , 
 */

//  import
@import '../../styles/variables';

//    (  )
$brand-brown: #7c746a;
$brand-brown-hover: #696158;
$brand-brown-light: rgba(124, 116, 106, 0.1);
$background-beige: #f7e8d3;
$white: #ffffff;

//  
$gray-900: #1a202c;
$gray-800: #2d3748;
$gray-700: #4a5568;
$gray-600: #718096;
$gray-500: #a0aec0;
$gray-400: #cbd5e0;
$gray-300: #e2e8f0;
$gray-200: #edf2f7;
$gray-100: #f7fafc;
$gray-50: #f7fafc;

//  
$success: #48bb78;
$warning: #ed8936;
$error: #f56565;
$info: #4299e1;

//  
$success: #48bb78;
$warning: #ed8936;
$error: #f56565;
$info: #4299e1;

//  
$spacing-xs: 0.5rem;   // 8px
$spacing-sm: 0.75rem;  // 12px
$spacing-md: 1rem;     // 16px
$spacing-lg: 1.5rem;   // 24px
$spacing-xl: 2rem;     // 32px
$spacing-2xl: 3rem;    // 48px

//  
$font-xs: 0.75rem;   // 12px
$font-sm: 0.875rem;  // 14px
$font-base: 1rem;    // 16px
$font-lg: 1.125rem;  // 18px
$font-xl: 1.25rem;   // 20px
$font-2xl: 1.5rem;   // 24px
$font-3xl: 1.875rem; // 30px

//  
$font-weight-normal: 400;
$font-weight-medium: 500;
$font-weight-semibold: 600;
$font-weight-bold: 700;

//  
$border-radius-sm: 0.25rem;  // 4px
$border-radius-md: 0.375rem; // 6px
$border-radius-lg: 0.5rem;   // 8px
$border-radius-xl: 0.75rem;  // 12px
$border-radius-full: 9999px; //  

// 
$shadow-sm: 0 1px 2px rgba(0, 0, 0, 0.05);
$shadow-md: 0 4px 6px rgba(0, 0, 0, 0.07);
$shadow-lg: 0 10px 15px rgba(0, 0, 0, 0.1);

//    (  )
.comprehensive-analysis-content {
  padding: $spacing-lg $spacing-md $spacing-2xl; // , , 
  background: transparent; //   
  min-height: calc(100vh - 120px);
  font-family: 'Pretendard', -apple-system, BlinkMacSystemFont, sans-serif;
  
  //   
  @media (max-width: 768px) {
    padding: $spacing-md $spacing-sm $spacing-xl; //    
  }
  
  @media (min-width: 768px) {
    max-width: 1200px;
    margin: 0 auto;
    padding: $spacing-xl $spacing-lg $spacing-2xl; //    
  }
}

.comprehensive-analysis-page {
  min-height: 100vh;
  background: $background-beige;
  font-family: 'Pretendard', -apple-system, BlinkMacSystemFont, sans-serif;
  
  //   
  padding: 0;
  
  @media (min-width: 768px) {
    padding: $spacing-lg;
  }
}

//  
.analysis-header {
  background: $white;
  border-bottom: 1px solid $gray-200;
  padding: $spacing-lg;
  margin-bottom: $spacing-xl;
  
  @media (min-width: 768px) {
    border-radius: $border-radius-xl;
    box-shadow: $shadow-md;
    margin-bottom: $spacing-2xl;
  }
  
  .header-content {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    gap: $spacing-lg;
  }
  
  .back-button {
    background: none;
    border: none;
    color: $brand-brown;
    font-size: $font-base;
    font-weight: $font-weight-medium;
    cursor: pointer;
    padding: $spacing-sm;
    border-radius: $border-radius-md;
    transition: all 0.2s ease;
    
    &:hover {
      background: $brand-brown-light;
      color: $brand-brown-hover;
    }
  }
  
  .header-text {
    flex: 1;
  }
  
  .page-title {
    font-size: $font-2xl;
    font-weight: $font-weight-bold;
    color: $gray-900;
    margin: 0 0 $spacing-xs 0;
    
    @media (min-width: 768px) {
      font-size: $font-3xl;
    }
  }
  
  .page-subtitle {
    font-size: $font-sm;
    color: $gray-600;
    margin: 0;
    line-height: 1.5;
  }
}

//   
.analysis-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 $spacing-md;
  display: flex;
  flex-direction: column;
  gap: $spacing-xl;
  
  @media (min-width: 768px) {
    padding: 0;
  }
}

//   
%card-base {
  background: $white;
  border-radius: $border-radius-lg;
  box-shadow: $shadow-md;
  border: 1px solid $gray-200;
  overflow: hidden;
  transition: all 0.2s ease;
  
  &:hover {
    box-shadow: $shadow-lg;
  }
}

//   
%card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: $spacing-lg;
  border-bottom: 1px solid $gray-200;
  background: #a69b8f; //     
  
  .section-title {
    font-size: $font-xl;
    font-weight: $font-weight-bold;
    color: $white; //     
    margin: 0;
  }
}

//  
%badge-base {
  display: inline-flex;
  align-items: center;
  padding: $spacing-xs $spacing-sm;
  border-radius: $border-radius-md;
  font-size: $font-xs;
  font-weight: $font-weight-medium;
  
  .badge-text {
    margin: 0;
  }
}

// AI    (  )
.analysis-card {
  background: $white;
  border-radius: $border-radius-xl;
  box-shadow: $shadow-md;
  overflow: hidden;
  border: 1px solid $gray-200;
  
  //     (24px)
  margin-bottom: $spacing-lg;
  
  //     
  &:last-child {
    margin-bottom: 0;
  }
  
  @media (min-width: 768px) {
    //     (32px)
    margin-bottom: $spacing-xl;
    
    &:last-child {
      margin-bottom: 0;
    }
  }
  
  .card-header {
    padding: $spacing-xs $spacing-lg $spacing-xs; //   : , , 
    border-bottom: 1px solid $gray-200;
    background: #8b7355; //  
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    .section-title {
      font-size: $font-lg; //   
      font-weight: $font-weight-semibold;
      color: $white; //     
      margin: 0;
    }
    
    .chart-info {
      display: flex;
      align-items: center;
      gap: $spacing-sm;
      margin-top: $spacing-xs; //  
      
      .info-text {
        font-size: $font-xs; //    (sm  xs)
        color: #f5f5dc; //   ( $gray-600 )
        font-weight: $font-weight-normal; //    (medium  normal)
        background: transparent; //   ( $gray-100)
        padding: 0; //   ( $spacing-xs $spacing-sm)
        border-radius: 0; //   
        border: none; //   ( 1px solid $gray-200)
        margin: 0;
      }
    }
  }
  
  .analysis-badge {
    background: $brand-brown-light;
    color: $brand-brown;
    padding: $spacing-xs $spacing-sm;
    border-radius: $border-radius-md;
    font-size: $font-sm;
    font-weight: $font-weight-medium;
  }
  
  .analysis-results {
    padding: $spacing-lg;
  }
  
  //     (  )
  .analysis-loading,
  .analysis-results,
  .summary-section,
  .insights-section,
  .chart-wrapper {
    padding: $spacing-lg;
  }
  
  .summary-section {
    margin-bottom: $spacing-xl;
    
    .subsection-title {
      font-size: $font-lg;
      font-weight: $font-weight-semibold;
      color: $gray-800;
      margin: 0 0 $spacing-md 0;
    }
    
    .summary-content {
      background: $gray-50;
      padding: $spacing-lg;
      border-radius: $border-radius-md;
      border-left: 4px solid $brand-brown;
    }
    
    .summary-text {
      font-size: $font-base;
      color: $gray-700;
      line-height: 1.6;
      margin: 0;
    }
  }
  
  .insights-section {
    .subsection-title {
      font-size: $font-lg;
      font-weight: $font-weight-semibold;
      color: $gray-800;
      margin: 0 0 $spacing-lg 0;
    }
  }
  
  .insights-grid {
    display: grid;
    gap: $spacing-md;
    
    @media (min-width: 768px) {
      grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    }
  }
  
  .insight-item {
    background: $white;
    border: 1px solid $gray-200;
    border-radius: $border-radius-md;
    padding: $spacing-lg;
    transition: all 0.2s ease;
    
    &.good {
      border-left: 4px solid $success;
      
      .status-indicator {
        background: rgba($success, 0.1);
        color: $success;
      }
    }
    
    &.warning {
      border-left: 4px solid $warning;
      
      .status-indicator {
        background: rgba($warning, 0.1);
        color: $warning;
      }
    }
    
    &.danger {
      border-left: 4px solid $error;
      
      .status-indicator {
        background: rgba($error, 0.1);
        color: $error;
      }
    }
    
    &:hover {
      box-shadow: $shadow-sm;
    }
  }
  
  .insight-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: $spacing-sm;
  }
  
  .insight-category {
    font-size: $font-lg;
    font-weight: $font-weight-semibold;
    color: $gray-800;
    margin: 0;
  }
  
  .status-indicator {
    @extend %badge-base;
    font-size: $font-xs;
  }
  
  .insight-message {
    font-size: $font-sm;
    color: $gray-600;
    line-height: 1.5;
    margin: 0 0 $spacing-sm 0;
  }
  
  .insight-recommendation {
    font-size: $font-sm;
    color: $gray-700;
    background: $gray-50;
    padding: $spacing-sm;
    border-radius: $border-radius-sm;
    margin: 0;
    
    strong {
      color: $gray-800;
    }
  }
  
  //   - AI    
  .analysis-loading {
    padding: $spacing-lg; //   (2xl  lg)
    text-align: center;
    min-height: 200px; //   
  }
  
  .loading-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: $spacing-lg;
  }
  
  .loading-spinner {
    .spinner-icon {
      width: 48px;
      height: 48px;
      animation: spin 2s linear infinite;
      filter: opacity(0.8);
    }
  }
  
  .loading-text {
    font-size: $font-base;
    color: $gray-600;
    margin: 0;
  }
  
  .analyze-button {
    background: $brand-brown;
    color: $white;
    border: none;
    padding: $spacing-md $spacing-xl;
    border-radius: $border-radius-lg;
    font-size: $font-base;
    font-weight: $font-weight-medium;
    cursor: pointer;
    transition: all 0.2s ease;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: $spacing-sm;
    
    &:hover:not(:disabled) {
      background: $brand-brown-hover;
      transform: translateY(-1px);
    }
    
    &:disabled {
      opacity: 0.6;
      cursor: not-allowed;
    }
    
    .button-spinner {
      width: 20px;
      height: 20px;
      animation: spin 1s linear infinite;
      filter: brightness(0) invert(1); //  
    }
  }
}

//   ( )
.chart-wrapper {
  padding: $spacing-lg;
  min-height: 350px;
  display: flex;
  align-items: center;
  justify-content: center;
  
  //    ( )
  .chart-loading {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    min-height: 250px; //   (300px  250px)
    background: $gray-50;
    border-radius: $border-radius-md;
    border: 1px dashed $gray-300;
    padding: $spacing-lg; //   (2xl  lg)
    text-align: center;
    
    .loading-spinner {
      margin-bottom: $spacing-lg;
    
      .spinner-icon {
        width: 48px;
        height: 48px;
        animation: spin 2s linear infinite;
        filter: opacity(0.7);
      }
    }
    
    .loading-text {
      font-size: $font-base;
      font-weight: $font-weight-medium;
      color: $gray-600;
      margin: 0;
      text-align: center;
    }
  }
}

// chart-info     ( )

//    ( )
.chart-card {
  @extend .analysis-card;
  
  .card-header {
    @extend %card-header;
  }
  
  .chart-info {
    @extend %badge-base;
    background: $info;
    color: $white;
  }
  
  .chart-wrapper {
    padding: $spacing-lg;
    overflow-x: auto;
    scroll-behavior: smooth;
    
    //  
    &::-webkit-scrollbar {
      height: 6px;
    }
    
    &::-webkit-scrollbar-track {
      background: $gray-100;
      border-radius: 3px;
    }
    
    &::-webkit-scrollbar-thumb {
      background: $gray-300;
      border-radius: 3px;
      
      &:hover {
        background: $gray-400;
      }
    }
  }
}

//   
.interaction-card {
  @extend %card-base;
  
  .card-header {
    @extend %card-header;
  }
  
  .safety-badge {
    @extend %badge-base;
    background: rgba($success, 0.1);
    color: $success;
  }
  
  .interactions-list {
    padding: $spacing-lg;
    display: flex;
    flex-direction: column;
    gap: $spacing-md;
  }
  
  .interaction-item {
    border: 1px solid $gray-200;
    border-radius: $border-radius-md;
    padding: $spacing-lg;
    
    &.high {
      border-left: 4px solid $error;
      background: rgba($error, 0.02);
    }
    
    &.medium {
      border-left: 4px solid $warning;
      background: rgba($warning, 0.02);
    }
    
    &.low {
      border-left: 4px solid $info;
      background: rgba($info, 0.02);
    }
  }
  
  .interaction-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: $spacing-sm;
  }
  
  .drug-combination {
    font-size: $font-lg;
    font-weight: $font-weight-semibold;
    color: $gray-800;
    margin: 0;
  }
  
  .severity-badge {
    @extend %badge-base;
    
    &.high {
      background: rgba($error, 0.1);
      color: $error;
    }
    
    &.medium {
      background: rgba($warning, 0.1);
      color: $warning;
    }
    
    &.low {
      background: rgba($info, 0.1);
      color: $info;
    }
  }
  
  .interaction-description {
    font-size: $font-sm;
    color: $gray-600;
    margin: 0 0 $spacing-sm 0;
    line-height: 1.5;
  }
  
  .interaction-recommendation {
    font-size: $font-sm;
    color: $gray-700;
    background: $gray-50;
    padding: $spacing-sm;
    border-radius: $border-radius-sm;
    
    strong {
      color: $gray-800;
    }
  }
  
  .no-interactions {
    padding: $spacing-2xl;
    text-align: center;
    
    .safe-icon {
      font-size: $font-3xl;
      margin-bottom: $spacing-md;
    }
    
    .safe-message {
      font-size: $font-base;
      color: $gray-600;
      margin: 0;
      line-height: 1.5;
    }
  }
}

//   
.nutrition-card {
  @extend %card-base;
  
  .card-header {
    @extend %card-header;
  }
  
  .nutrition-badge {
    @extend %badge-base;
    background: rgba($success, 0.1);
    color: $success;
  }
  
  .nutrition-content {
    padding: $spacing-lg;
  }
  
  .nutrition-grid {
    display: grid;
    gap: $spacing-xl;
    
    @media (min-width: 768px) {
      grid-template-columns: 1fr 1fr;
    }
  }
  
  .nutrition-section {
    .section-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: $spacing-lg;
    }
    
    .nutrition-title {
      font-size: $font-lg;
      font-weight: $font-weight-semibold;
      margin: 0;
    }
    
    .warning-icon,
    .recommend-icon {
      font-size: $font-lg;
    }
  }
  
  .avoid-section {
    .nutrition-title {
      color: $error;
    }
  }
  
  .recommend-section {
    .nutrition-title {
      color: $success;
    }
  }
  
  .nutrition-list {
    display: flex;
    flex-direction: column;
    gap: $spacing-md;
  }
  
  .nutrition-item {
    padding: $spacing-md;
    border-radius: $border-radius-md;
    border: 1px solid $gray-200;
    
    .food-name {
      font-size: $font-base;
      font-weight: $font-weight-semibold;
      margin: 0 0 $spacing-xs 0;
    }
    
    .food-reason,
    .food-benefit {
      font-size: $font-sm;
      color: $gray-600;
      margin: 0;
      line-height: 1.4;
    }
  }
  
  .avoid-item {
    background: rgba($error, 0.02);
    border-left: 3px solid $error;
    
    .food-name {
      color: $error;
    }
  }
  
  .recommend-item {
    background: rgba($success, 0.02);
    border-left: 3px solid $success;
    
    .food-name {
      color: $success;
    }
  }
}

//   
.disclaimer-card {
  @extend %card-base;
  background: $gray-50;
  border: 1px solid $gray-300;
  
  .disclaimer-content {
    padding: $spacing-lg;
  }
  
  .disclaimer-title {
    font-size: $font-lg;
    font-weight: $font-weight-semibold;
    color: $gray-800;
    margin: 0 0 $spacing-lg 0;
  }
  
  .disclaimer-text {
    margin-bottom: $spacing-lg;
    
    p {
      font-size: $font-sm;
      color: $gray-600;
      margin: 0 0 $spacing-xs 0;
      line-height: 1.5;
    }
  }
  
  .data-source {
    border-top: 1px solid $gray-300;
    padding-top: $spacing-md;
    
    p {
      font-size: $font-xs;
      color: $gray-500;
      margin: 0 0 $spacing-xs 0;
      
      strong {
        color: $gray-600;
      }
    }
  }
}

//    
.title-subtitle {
  font-size: $font-sm;
  color: $gray-600;
  margin: $spacing-xs 0 0 0;
  line-height: 1.4;
}

//   
.disclaimer-box {
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: $border-radius-md;
  margin-top: $spacing-lg;
  padding: $spacing-lg;
  
  .disclaimer-content {
    .disclaimer-title {
      font-size: $font-lg;
      font-weight: $font-weight-semibold;
      color: $gray-700;
      margin: 0 0 $spacing-md 0;
      text-align: center;
    }
    
    .disclaimer-text {
      margin-bottom: $spacing-md;
      
      p {
        font-size: $font-sm;
        color: $gray-600;
        line-height: 1.5;
        margin: $spacing-xs 0;
      }
    }
    
    .data-source {
      border-top: 1px solid #e9ecef;
      padding-top: $spacing-md;
      
      p {
        font-size: $font-xs;
        color: $gray-500;
        text-align: center;
        margin: 0;
      }
    }
  }
}

//  
.error-message {
  background: rgba($error, 0.1);
  border: 1px solid rgba($error, 0.3);
  border-radius: $border-radius-md;
  padding: $spacing-lg;
  text-align: center;
  
  p {
    color: $error;
    font-size: $font-base;
    margin: 0;
  }
}

// 
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

//  
@media (max-width: 767px) {
  .comprehensive-analysis-page {
    .analysis-content {
      gap: $spacing-lg;
    }
    
    .insights-grid {
      grid-template-columns: 1fr;
    }
    
    .nutrition-grid {
      grid-template-columns: 1fr;
      gap: $spacing-lg;
    }
    
    .chart-wrapper {
      overflow-x: scroll;
      
      > * {
        min-width: 600px;
      }
    }
  }
}

//    (  -  )
.status-icon {
  width: 24px;
  height: 24px;
  display: inline-block;
  
  &.warning {
    filter: hue-rotate(30deg) saturate(1.5); //  
  }
  
  &.recommend {
    filter: hue-rotate(120deg) saturate(1.2); //  
  }
}

//   ( )
.safe-icon {
  display: flex;
  justify-content: center;
  margin-bottom: $spacing-md;
  
  .status-icon {
    width: 48px;
    height: 48px;
    filter: hue-rotate(120deg) saturate(1.2); //  
  }
}

//    ( )
.section-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: $spacing-md;
  
  .nutrition-title {
    font-size: $font-lg;
    font-weight: $font-weight-semibold;
    color: $gray-900;
    margin: 0;
  }
  
  .warning-icon,
  .recommend-icon {
    display: flex;
    align-items: center;
  }
}

//    ( )
.chart-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: $spacing-lg; //   (2xl  lg)
  text-align: center;
  
  .loading-spinner {
    margin-bottom: $spacing-md;
    
    .spinner-icon {
      width: 48px;
      height: 48px;
      animation: spin 2s linear infinite;
      filter: opacity(0.8);
    }
  }
  
  .loading-text {
    font-size: $font-base;
    color: $gray-600;
    margin: 0;
  }
}

//     -    
// Health Metrics Wrapper (  )
.health-metrics-wrapper {
  padding: 0 $spacing-lg; //   wrapper 
  
  @media (max-width: 480px) {
    padding: 0 $spacing-md; //   
  }
}

// Health Metrics Container
.health-metrics-container {
  position: relative;
}

.health-metrics-slider {
  display: flex;
  gap: $spacing-md; //   
  overflow-x: auto;
  padding: $spacing-xs 0 $spacing-sm 0; //   ,    (24px  12px)
  scroll-snap-type: x mandatory;
  scroll-behavior: smooth;
  
  //   (  )
  &::-webkit-scrollbar {
    display: none;
  }
  -ms-overflow-style: none;
  scrollbar-width: none;
}

// Dot Indicators
.slider-dots {
  display: flex;
  justify-content: center;
  gap: $spacing-xs;
  margin: $spacing-xs 0 $spacing-sm 0; //    (12px 0 24px 0  8px 0 12px 0)
  
  .dot {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: $gray-300;
    cursor: pointer;
    transition: all 0.3s ease;
    
    &.active {
      background: $brand-brown;
      transform: scale(1.2);
    }
    
    &:hover {
      background: $brand-brown-hover;
      transform: scale(1.1);
    }
  }
}

.health-metric-card {
  min-width: 300px;
  max-width: 320px;
  flex-shrink: 0;
  background: rgba(255, 255, 255, 0.9);
  border: 1px solid #f5f5dc; //   
  border-radius: $border-radius-lg;
  padding: $spacing-lg;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  scroll-snap-align: start;
  position: relative;
  box-shadow: $shadow-sm;
  
  //       
  
  &:hover {
    background: rgba(255, 255, 255, 0.95);
    box-shadow: $shadow-md;
    transform: translateY(-3px);
    border-color: $brand-brown;
  }
  
  //   (     )
  &.highlight {
    border: 2px solid #3b82f6 !important;
    box-shadow: 0 4px 16px rgba(59, 130, 246, 0.3) !important;
    transform: scale(1.02) !important;
    background: rgba(59, 130, 246, 0.05) !important;
  }
  
  .metric-header {
    display: flex;
    flex-direction: column;
    gap: 4px; // -    (8px  4px)
    margin-bottom: 8px; // -    (12px  8px)
    position: relative;
      
      .status-badge {
        position: absolute;
        top: 0;
        right: 0;
        padding: 6px 10px;
        border-radius: 8px;
        font-size: 10px;
        font-weight: 600;
        color: white;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2px;
        min-width: 50px;
        
        &.status-normal {
          background: $brand-brown;
        }
        
        &.status-warning {
          background: $warning;
        }
        
        &.status-abnormal {
          background: #ef4444;
        }
        
        &.status-neutral {
          background: $gray-500;
        }
        
        .status-text {
          font-size: 10px;
          font-weight: 600;
          line-height: 1;
        }
        
        .status-date {
          font-size: 8px;
          font-weight: 400;
          opacity: 0.9;
          line-height: 1;
          white-space: nowrap;
        }
      }
      
      .metric-title {
        font-size: $font-base; // $font-lg $font-base  (-2 )
        font-weight: 600;
        color: $gray-800;
        margin: 0;
        padding-right: 60px; //   
      }
      
      .metric-value {
        display: flex;
        align-items: baseline;
        gap: 6px;
        
        .value {
          font-size: $font-xl; // $font-2xl $font-xl  (-2 )
          font-weight: 700;
          color: $brand-brown;
        }
        
        .unit {
          font-size: $font-sm; // $font-base $font-sm  (-2 )
          color: $gray-600;
          font-weight: 500;
        }
      }
    }
    
    .metric-chart {
      width: 100%;
      height: 170px; //     (160px  170px,    )
      border-radius: $border-radius-md;
      background: rgba(245, 245, 220, 0.3); //    
      padding: 0; //     
      display: flex;
      align-items: center;
      justify-content: center;
      
      .no-data {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 100%;
        color: $gray-500;
        font-size: $font-sm;
        
        p {
          margin: 0;
        }
      }
      
      .single-data {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        height: 100%;
        gap: $spacing-xs;
        
        .single-point {
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 4px;
          
          .point-dot {
            width: 12px;
            height: 12px;
            border-radius: 50%;
            background: $brand-brown;
            box-shadow: 0 0 0 3px rgba(124, 116, 106, 0.2);
            animation: pulse 2s infinite;
          }
          
          .point-value {
            font-size: $font-sm;
            font-weight: 600;
            color: $brand-brown;
          }
        }
        
        .single-data-label {
          font-size: $font-xs;
          color: $gray-500;
          margin: 0;
        }
      }
    }
  }

//    
@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(124, 116, 106, 0.4);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(124, 116, 106, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(124, 116, 106, 0);
  }
}

//  
@media (max-width: 480px) {
  .health-metrics-slider {
    gap: $spacing-sm; //    
    padding: $spacing-xs 0 $spacing-sm 0; //    ,   
    
    .health-metric-card {
      min-width: 280px; //    
      max-width: 300px;
      padding: $spacing-md;
      
      //        
      
      .metric-header {
        gap: 3px; //  -   
        margin-bottom: 6px; //  -   
        
        .metric-title {
          font-size: $font-sm; //   
          padding-right: 50px;
        }
        
        .metric-value {
          .value {
            font-size: $font-lg; //   
          }
          
          .unit {
            font-size: $font-sm;
          }
        }
      }
      
      .metric-chart {
        height: 150px; //     (140px  150px,    )
      }
    }
  }
}

//   Wrapper (  )
.visit-trends-wrapper {
  padding: 0 $spacing-lg; //   wrapper 
  
  @media (max-width: 480px) {
    padding: 0 $spacing-md; //   
  }
}

.visit-trends-container {
  position: relative;
}

.visit-trends-slider {
  display: flex;
  gap: $spacing-md;
  overflow-x: auto;
  padding: $spacing-xs 0 $spacing-sm 0; //    
  scroll-snap-type: x mandatory;
  scroll-behavior: smooth;
  
  //  
  &::-webkit-scrollbar {
    display: none;
  }
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.visit-trends-dots {
  display: flex;
  justify-content: center;
  gap: $spacing-xs;
  margin: $spacing-xs 0 $spacing-sm 0; //    (12px 0 24px 0  8px 0 12px 0)
  
  .dot {
    width: 6px; //  
    height: 6px; //  
    border-radius: 50%;
    background: $gray-300;
    cursor: pointer;
    transition: all 0.2s ease; //   
    
    &.active {
      background: $brand-brown;
      transform: scale(1.3); //    
    }
    
    &:hover {
      background: $brand-brown-hover;
      transform: scale(1.1);
    }
  }
}

//   
.analysis-progress {
  margin: $spacing-lg 0;
  
  .progress-bar-container {
    width: 100%;
    height: 8px;
    background-color: $gray-200;
    border-radius: $border-radius-full;
    overflow: hidden;
    margin-bottom: $spacing-sm;
    
    .progress-bar {
      height: 100%;
      background: linear-gradient(90deg, $brand-brown, $brand-brown-hover);
      border-radius: $border-radius-full;
      transition: width 0.3s ease;
      position: relative;
      
      //    
      &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: linear-gradient(
          90deg,
          transparent,
          rgba(255, 255, 255, 0.3),
          transparent
        );
        animation: shimmer 2s infinite;
      }
    }
  }
  
  .progress-text {
    text-align: center;
    font-size: $font-sm;
    color: $gray-600;
    margin: 0;
    font-weight: $font-weight-medium;
  }
}

@keyframes shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

//  
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
  
  .analysis-progress .progress-bar::after {
    animation: none;
  }
}

//  visit-trends-slider   (   )

.visit-trend-card {
  flex: 0 0 auto;
  background: $white;
  border: 1px solid #f5f5dc; //   
  border-radius: $border-radius-md;
  padding: $spacing-lg; //     (24px)
  scroll-snap-align: start;
  
  // wrapper     
  
  //     
  min-width: 300px;
  max-width: 320px;
  flex-shrink: 0;
  
  //    
  @media (max-width: $breakpoint-md) {
    min-width: 280px;
    max-width: 300px;
  }
  
  //     (wrapper  )
  
  .trend-header {
    margin-bottom: $spacing-md;
    
    .trend-title {
      font-size: $font-lg;
      font-weight: $font-weight-semibold;
      color: $gray-800;
      margin: 0;
    }
  }
  
  .trend-chart {
    .chart-loading {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      height: 200px; //   (250px  200px)
      
      .loading-spinner {
        margin-bottom: $spacing-sm;
        
        .spinner-icon {
          width: 24px;
          height: 24px;
          animation: spin 1s linear infinite;
          filter: hue-rotate(30deg);
        }
      }
      
      .loading-text {
        font-size: $font-sm;
        color: $gray-600;
        margin: 0;
      }
    }
  }
}